import Fuse from "fuse.js"
import { graphql, useStaticQuery } from "gatsby";
import React from "react";

export function FuzzySearch() {
    
    const nodes = useStaticQuery(BlogMetaData).allMdx.nodes;
    const searchFilter = new Fuse(nodes, 
            { 
                keys: [
                    "frontmatter.title",
                    "frontmatter.description",
                    "frontmatter.author"
                ]
            });

    const s = (filter) => {
        return searchFilter.search(filter)
    }

    return s;
}

export default FuzzySearch;


// GraphQL Query

const BlogMetaData = graphql`
  query {
    allMdx {
      nodes {
        frontmatter {
          title
          description
          author
          slug
        }
      }
    }
  }
`;